import axios from 'axios';

export const signup = ({username, password}, config) => axios.post(`/auth-api/`, {username, password}, config);
export const login = ({username, password}) => axios.post(`/api-token-auth/`, {username, password});
export const checkLogin = ({token}) => axios.post(`/api-token-verify/`, {token});
export const getBookmarkList = () => axios.get(`/api/bookmarks/`); 
export const postBookmark = ({site_title, site_url}, config) => axios.post(`/api/bookmarks/`, {site_title, site_url}, config);
export const readBookmarkList = (id) => axios.get(`/api/bookmarks/${id}`);
export const patchBookmark = ({id, site_title, site_url}) => axios.patch(`/api/bookmarks/${id}/`, {site_title, site_url});
export const removeBookmark = (id) => axios.delete(`/api/bookmarks/${id}`);


